@import url("https://fonts.googleapis.com/css2?family=Allison&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Allison&display=swap");
.home {
  margin: 0 auto 50px;

  width: 960px;
}
.home > h2 {
  padding: 20px;
  font-family: "Allison", cursive;
  font-family: "Besley", serif;
  font-size: 40px;
}
.home > img {
  width: 100%;
  margin: 0 auto;
  object-fit: contain;
}
.home > address {
  font-size: 20px;
  font-weight: 500;
  padding: 10px;
  display: flex;
  justify-content: flex-end;
}
.home > p {
  font-size: 18px;
}
