.box {
  width: 400px;
  margin: auto;
}
.box > input {
  width: 100%;
  height: 30px;
  font-size: 20px;
}
.box > button {
  width: 60px;
  height: 30px;
}
.render {
  width: 400px;
  margin: auto;
  line-height: 40px;
  font-size: 25px;
}
i {
  padding: 10px;
}
.item {
  display: flex;
  cursor: pointer;
}
