@import url("https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap");
header {
  width: 100vw;
  display: flex;
  display: flex;
  justify-content: space-between;
  background-color: teal;
  height: 50px;
}
header ul {
  display: flex;
  padding-right: 200px;
}
header ul li {
  padding: 10px;
  justify-content: flex-end;
  font-size: 1rem;
}
.logo {
  font-size: 1.7rem;
  color: aliceblue;
}
.iconLogo {
  color: white;
  font-weight: 500;
  font-family: "Dancing Script", cursive;
  padding-left: 50px;
}
