.container {
  height: 150vh;
  width: 100%;
  background-image: url("https://i.pinimg.com/736x/9d/c8/51/9dc8516895c763db4cc6594ee15f84d8.jpg");
}
h1 {
  text-align: center;
}
.myInput {
  display: flex;
  margin: 0 auto;
  width: 300px;
}
.myInput > input {
  padding: 2px 5px;
}
.myInput > button {
  padding: 2px 20px;
}
.myOutput {
  text-align: center;
}
.myOutput > ul > li {
  font-size: 40px;
  font-weight: 500;
  padding: 5px 20px;
}
.btn {
  padding: 4px 20px;
  margin-left: 20px;
  transform: translateY(-10px);
  background-color: #81ecec;
}
